// 引入标题组件
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 引入标题元素组件
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
// 导入所需接口
import {
  extendPropertyGetListExtendPropertyTypeAction,
  extendPropertyGetListExtendPropertyDataTypeAction,
  extendPropertyInsertBatchAction
} from '@/api/formConfig';
import {
  showSuccessMessage,
  showWarningMessage
} from '@/utils/message';
import { TITLE, STATUS_CODE, MSG, VARIABLE, BTN_TEXT, CONSTANTS_FORM, FORM_CONFIG_DATE } from '@/utils/constantsFile';
import { showErrorNotify } from '@/utils/notify';
export default {
  components: {
    // 加载标题组件
    HeaderTitle,
    HeadOperationItem
  },
  props: {
  },
  data() {
    const lengthValid = ({ row, cellValue }) => {
      return new Promise((resolve, reject) => {
        if (row.propertyType === 2) {
          var r = /^\+?[1-9][0-9]*$/;// 正整数let
          if (!r.test(cellValue)) {
            reject(new Error('请输入正整数'));
          } else if (Number(cellValue) > 25 || Number(cellValue) < 1) {
            reject(new Error('请输入范围1-25的数字'));
          } else {
            resolve();
          }
        } else if (row.propertyType === 1) {
          var x = /^\+?[1-9][0-9]*$/;// 正整数
          if (!x.test(cellValue)) {
            reject(new Error('请输入正整数'));
          } else if (Number(cellValue) > 255 || Number(cellValue) < 1) {
            reject(new Error('请输入范围1-255的数字'));
          } else {
            resolve();
          }
        } else {
          resolve();
        }
      });
    };

    const decimalPointValid = ({ row, cellValue }) => {
      return new Promise((resolve, reject) => {
        if (row.propertyType === 2) {
          var r = /^\+?[0-9][0-9]*$/;// 正整数let
          if (!r.test(cellValue)) {
            reject(new Error('请输入正整数'));
          } else if (Number(cellValue) > 8 || Number(cellValue) < 0) {
            reject(new Error('请输入范围0-8的数字'));
          } else {
            resolve();
          }
        } else {
          resolve();
        }
      });
    };

    return {
      BTN_TEXT: BTN_TEXT,
      FORM_CONFIG_DATE: FORM_CONFIG_DATE,
      imageUrl: '',
      // 页面标题
      titleName: CONSTANTS_FORM.FORM_ADD,
      // 表单验证
      validRules: {
        // propertyCode: [{ required: true, message: MSG.PLEASE_ENTER_CONTENT }],
        extendType: [{ required: true, message: MSG.PLEASE_ENTER_CONTENT }],
        extendName: [
          { required: true, message: MSG.PLEASE_ENTER_CONTENT },
          { max: 10, message: '长度不得大于255个字符' }
        ],
        propertyType: [{ required: true, message: MSG.PLEASE_ENTER_CONTENT }],
        propertyLength: [
          { required: true, message: MSG.PLEASE_ENTER_CONTENT },
          { validator: lengthValid }
        ],
        propertyDecimalPoint: [
          { required: true, message: MSG.PLEASE_ENTER_CONTENT },
          { validator: decimalPointValid }
        ],
        propertyIsNull: [{ required: true, message: MSG.PLEASE_ENTER_CONTENT }],
        remark: [{ max: 100, message: '长度不得大于255个字符' }]
      },
      // 可编辑配置
      editConfig: {
        trigger: 'click',
        mode: 'row',
        showStatus: true
        // icon: 'fa fa-file-text-o'
      },
      // table数据
      tableDatas: [],
      // table表头配置
      tableColumns: [
        // {
        //   field: 'propertyCode',
        //   title: CONSTANTS_FORM.FIELD_CODE,
        //   align: 'center',
        //   editRender: { name: 'input' }
        // },
        {
          field: 'extendType',
          title: CONSTANTS_FORM.MODEL_NAME,
          align: 'center',
          editRender: { name: '$select', options: [] }
        },
        {
          field: 'extendName',
          title: CONSTANTS_FORM.FIELD_NAME,
          align: 'center',
          editRender: { name: 'input' }
        },
        {
          field: 'propertyType',
          title: CONSTANTS_FORM.FIELD_TYPE,
          align: 'center',
          editRender: { name: '$select', options: [], events: { change: this.sexChangeEvent } }
        },
        {
          field: 'propertyLength',
          title: CONSTANTS_FORM.FIELD_LENGTH,
          align: 'center',
          editRender: {},
          slots: {
            // 使用 JSX 渲染
            default: ({ row }) => {
              if (row.propertyType !== 3) {
                return [
                  <span>{row.propertyLength}</span>
                ];
              } else {
                return [
                  <span>{FORM_CONFIG_DATE[row.propertyLength]}</span>
                ];
              }
            },
            edit: ({ row, cellValue }) => {
              if (row.propertyType === 1) {
                return [
                  <el-input type="number" v-model={row.propertyLength} min={1} max={255} onInput={() => this.propertyTypeInput(row)} ></el-input>
                ];
              } else if (row.propertyType === 2) {
                return [
                  <el-input type="number" v-model={row.propertyLength} min={1} max={8} ></el-input>
                ];
              } else if (row.propertyType === 3) {
                return [
                  <el-select v-model={row.propertyLength} >
                    {/* <el-option key={1} value={1} label={'YYYY-MM-DD HH:mm:ss'} ></el-option>
                    <el-option key={2} value={2} label={'YYYY-MM-DD'} ></el-option>
                    <el-option key={3} value={3} label={'HH:mm:ss'} ></el-option> */}
                    <el-option key={1} value={1} label={'年-月-日 时:分:秒'} ></el-option>
                    <el-option key={2} value={2} label={'年-月-日'} ></el-option>
                    <el-option key={3} value={3} label={'时:分:秒'} ></el-option>
                  </el-select>
                ];
              }
            }
          }
        },
        {
          field: 'propertyDecimalPoint',
          title: CONSTANTS_FORM.DECIMAL_POINT,
          align: 'center',
          editRender: { name: '$input', props: { disabled: true, type: 'number', min: 0, max: 8 } }
        },
        {
          field: 'propertyIsNull',
          title: CONSTANTS_FORM.NOT_NULL,
          align: 'center',
          editRender: {
            name: '$select',
            options: [
              {
                label: VARIABLE.YES,
                value: 1
              },
              {
                label: VARIABLE.NO,
                value: 0
              }
            ]
          }
        },
        {
          field: 'remark',
          title: CONSTANTS_FORM.ANNOTATION,
          align: 'center',
          editRender: { name: 'input' }
        },
        {
          field: '',
          title: BTN_TEXT.OPERATION,
          width: '100',
          align: 'center',
          slots: {
            default: ({ row, column }) => {
              return [
                <i
                  onClick={() => this.copyRow(row, column)}
                  class="icon-table-addRow"
                ></i>,
                <i
                  onClick={() => this.deleteRow(row, column)}
                  class="icon-table-delete"
                ></i>
              ];
            }
          }
        }
      ]
    };
  },
  computed: {},
  created() {
    this.getAllModel();
    this.getAllType();
  },
  mounted() {
    // 默认新增一行
    this.insertEvent();
  },
  methods: {
    // 按钮触发事件
    buttonClickEvent(type) {
      if (type === 'exit') {
        this.goBack();
        return;
      }
      this[`${type}`]();
    },
    async getAllModel() {
      try {
        const res = await extendPropertyGetListExtendPropertyTypeAction();
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          var arr = [];
          for (var key in res.rows) {
            var json = {};
            json.value = Number(key);
            json.label = res.rows[key];
            arr.push(json);
          }
          this.tableColumns[0].editRender.options = arr;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    async getAllType() {
      try {
        const res = await extendPropertyGetListExtendPropertyDataTypeAction();
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          var arr = [];
          for (var key in res.rows) {
            var json = {};
            json.value = Number(key);
            json.label = res.rows[key];
            arr.push(json);
          }
          this.tableColumns[2].editRender.options = arr;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 移除一行
     * @param row 行数据
     * @return: 无
     * @author: ZJM
     */
    deleteRow(row, column) {
      this.$XModal.confirm(MSG.CONFIRM_DELETE).then(type => {
        if (type === 'confirm') {
          this.$refs.xGrid.remove(row);
        }
      });
    },
    /**
     * @description: 执行新增一行
     * @param row 行数据
     * @return: 无
     * @author: ZJM
     */
    async insertEvent() {
      const record = {
        extendType: null,
        propertyType: 1,
        propertyIsNull: 0,
        propertyLength: 1,
        propertyDecimalPoint: 0
      };
      // 往表格插入临时数据，从指定位置插入一行或多行
      await this.$refs.xGrid.insertAt(record, -1);
    },
    async copyRow(row, column) {
      this.$refs.xGrid.clearActived();
      // const index = this.$refs.xGrid.$getRowIndex(row);
      const record = { ...row };
      delete record._XID;
      // 往表格插入临时数据，从指定位置插入一行或多行
      await this.$refs.xGrid.insertAt(record, row);
    },
    /**
     * @description: 表单提交
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    async submit() {
      try {
        const {
          // 新增的数据
          insertRecords
        } = this.$refs.xGrid.getRecordset();
        const data = JSON.parse(JSON.stringify(insertRecords));
        const res = await extendPropertyInsertBatchAction(
          {},
          data);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 返回主页面
          this.goBack();
          // 提示正常信息
          showSuccessMessage(res.message);
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 执行验证
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    async fullValidEvent() {
      const errMap = await this.$refs.xGrid
        .fullValidate()
        .catch(errMap => errMap);
      if (errMap) {
        var msgList = [];
        Object.values(errMap).forEach(errList => {
          errList.forEach((params, index) => {
            const { column, rules } = params;
            rules.forEach(rule => {
              msgList.push(
                `${VARIABLE.THE} ${index + 1} ${VARIABLE.ROW} ${column.title} ${MSG.VALIDATION_FAILED}：${rule.message}`
              );
            });
          });
        });
        this.$XModal.alert({
          status: 'error',
          message: () => {
            return [
              <div class="red" style="max-height: 400px;overflow: auto;">
                {msgList.map(msg => (
                  <div>{msg}</div>
                ))}
              </div>
            ];
          }
        });
      } else {
        // 通过验证，执行提交
        this.submit();
      }
    },
    /**
     * @description: 返回按钮点击
     * @param 无
     * @return:无
     * @author: ZJM
     */
    goBack() {
      this.$emit('goBack', VARIABLE.ZERO_NUMBER);
    },
    /**
     * @description: 字符类型下拉选择触发
     * @param 无
     * @return:无
     * @author: ZJM
     */
    sexChangeEvent({ row, column }) {
      if (row.propertyType === 1) {
        row.propertyLength = 1;
        row.propertyDecimalPoint = 0;
        this.tableColumns[4].editRender.props.disabled = true;
      } else if (row.propertyType === 2) {
        row.propertyLength = 1;
        row.propertyDecimalPoint = 0;
        this.tableColumns[4].editRender.props.disabled = false;
      } else if (row.propertyType === 3) {
        row.propertyLength = 1;
        row.propertyDecimalPoint = 0;
        this.tableColumns[4].editRender.props.disabled = true;
      }
    },
    propertyTypeInput(row) {
      // this.$refs.xGrid.setActiveRow(row);
      // setTimeout(() => {
      //   this.$refs.xGrid.setSelectCell(row, 'propertyLength');
      //   this.$refs.xGrid.setActiveCell(row, 'propertyLength');
      // }, 1000);
    },
    /**
     * @description: 激活时触发禁用
     * @param 无
     * @return:无
     * @author: ZJM
     */
    activeCellMethod({ column, row }) {
      if (row.propertyType === 1) {
        this.tableColumns[4].editRender.props.disabled = true;
      } else if (row.propertyType === 2) {
        this.tableColumns[4].editRender.props.disabled = false;
      } else if (row.propertyType === 3) {
        this.tableColumns[4].editRender.props.disabled = true;
      }
      return true;
    }
  }
};
