// 引入标题组件
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 引入标题元素组件
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
// 引入table组件
import VXETable from '@/components/common/Table';
// 引入提示组件
import {
  showSuccessMessage,
  showWarningMessage
} from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
// 引入类型信息组件
import addForm from '@/views/main/FormConfiguration/addForm.vue';
import { STATUS_CODE, TITLE, MSG, VARIABLE, BTN_TEXT, CONSTANTS_FORM, FORM_CONFIG_DATE } from '@/utils/constantsFile';
// 导入所需接口
import {
  extendPropertyGetAllAction,
  extendPropertyGetListExtendPropertyTypeAction,
  extendPropertyGetListExtendPropertyDataTypeAction,
  extendPropertyUpdateByIdAction,
  extendPropertyDeleteByIdsAction
} from '@/api/formConfig';
import { searchCondetionFunc } from '@/utils/publicFunction';
export default {
  name: 'FormConfiguration',
  components: {
    // 加载标题组件
    HeaderTitle,
    // 加载标题元素组件
    HeadOperationItem,
    // 加载table组件
    VXETable,
    // 加载新增设备组件
    addForm
  },
  data() {
    var lengthValid = (rule, value, callback) => {
      if (this.formUpdate.propertyType === 2) {
        var r = /^\+?[1-9][0-9]*$/;// 正整数let
        if (!r.test(value)) {
          callback(new Error('请输入正整数'));
        } else if (Number(value) > 25 || Number(value) < 1) {
          callback(new Error('请输入范围1-8的数字'));
        } else {
          callback();
        }
      } else if (this.formUpdate.propertyType === 1) {
        var x = /^\+?[1-9][0-9]*$/;// 正整数
        if (!x.test(value)) {
          callback(new Error('请输入正整数'));
        } else if (Number(value) > 255 || Number(value) < 1) {
          callback(new Error('请输入范围1-255的数字'));
        } else {
          callback();
        }
      } else {
        callback();
      }
    };

    var decimalPointValid = (rule, value, callback) => {
      if (this.formUpdate.propertyType === 2) {
        var r = /^\+?[0-9][0-9]*$/;// 正整数let
        if (!r.test(value)) {
          callback(new Error('请输入正整数'));
        } else if (Number(value) > 8 || Number(value) < 0) {
          callback(new Error('请输入范围0-8的数字'));
        } else {
          callback();
        }
      } else {
        callback();
      }
    };

    return {
      VARIABLE: VARIABLE,
      BTN_TEXT: BTN_TEXT,
      CONSTANTS_FORM: CONSTANTS_FORM,
      FORM_CONFIG_DATE: FORM_CONFIG_DATE,
      // 设备修改表单数据
      formUpdate: {},
      // 设备修改表单验证
      rulesUpdate: {
        propertyCode: [{ required: true, message: MSG.PLEASE_ENTER_CONTENT }],
        extendType: [{ required: true, message: MSG.PLEASE_ENTER_CONTENT }],
        extendName: [
          { required: true, message: MSG.PLEASE_ENTER_CONTENT },
          { max: 10, message: '长度不得大于255个字符' }
        ],
        propertyType: [{ required: true, message: MSG.PLEASE_ENTER_CONTENT }],
        propertyLength: [
          { required: true, message: MSG.PLEASE_ENTER_CONTENT },
          { validator: lengthValid }
        ],
        propertyDecimalPoint: [
          { required: true, message: MSG.PLEASE_ENTER_CONTENT },
          { validator: decimalPointValid }
        ],
        propertyIsNull: [{ required: true, message: MSG.PLEASE_ENTER_CONTENT }],
        remark: [{ max: 100, message: '长度不得大于255个字符' }]
      },
      // table多选数据
      dataChecks: [],
      tableName: 'device',
      // 用于操作当前显示哪个组件 0：主页面 1：新增子节点参数
      showPage: 0,
      // 表格查询表单数据
      formParams: {
        model: null
      },
      // 模态框名称
      titlename: '',
      // 控制模态框修改
      dialogVisibleUpdate: false,
      // table加载动画
      tableLoading: false,
      // table分页配置
      tablePage: {
        total: 0,
        currentPage: 1,
        pageSize: 10,
        pageSizes: [5, 10, 20, 50, 100, 200, 500],
        layouts: [
          'Sizes',
          'PrevJump',
          'PrevPage',
          'Number',
          'NextPage',
          'NextJump',
          'FullJump',
          'Total'
        ],
        perfect: true,
        slots: {
          // 使用自定义插槽
          left: 'pager_left'
        }
      },
      // table数据
      tableDatas: [],
      // table表头配置
      tableColumns: [
        {
          type: 'radio',
          align: 'center',
          visible: true,
          width: '50'
        },
        // { type: 'checkbox', width: '50', align: 'center' },
        {
          field: 'propertyCode',
          title: CONSTANTS_FORM.FIELD_CODE,
          align: 'center',
          width: ''
        },
        {
          field: 'extendType',
          title: CONSTANTS_FORM.MODEL_NAME,
          align: 'center',
          formatter: this.formatterModel
        },
        {
          field: 'extendName',
          title: CONSTANTS_FORM.FIELD_NAME,
          align: 'center'
        },
        {
          field: 'propertyType',
          title: CONSTANTS_FORM.FIELD_TYPE,
          align: 'center',
          formatter: this.formatterType
        },
        {
          field: 'propertyLength',
          title: CONSTANTS_FORM.FIELD_LENGTH,
          align: 'center',
          formatter: this.formatterLength
        },
        {
          field: 'propertyDecimalPoint',
          title: CONSTANTS_FORM.DECIMAL_POINT,
          align: 'center'
        },
        {
          field: 'propertyIsNull',
          title: CONSTANTS_FORM.NOT_NULL,
          align: 'center',
          formatter: this.formatterIsOrNo
        },
        {
          field: 'remark',
          title: CONSTANTS_FORM.ANNOTATION,
          align: 'center'
        }
        // {
        //   field: '',
        //   title: BTN_TEXT.OPERATION,
        //   width: '120',
        //   align: 'center',
        //   slots: {
        //     default: ({ row, column }) => {
        //       return [
        //         <el-dropdown
        //           placement="bottom"
        //           size="small"
        //           onCommand={(command) => this.handleCommand(command, row)}
        //         >
        //           <span class="operation-column" >
        //             {BTN_TEXT.OPERATION}
        //           </span>
        //           <el-dropdown-menu slot="dropdown">
        //             <el-dropdown-item class="operation-column" command="info"> {BTN_TEXT.MODIFY}</el-dropdown-item>
        //             <el-dropdown-item class="operation-column" command="delete">{BTN_TEXT.DELETE}</el-dropdown-item>
        //           </el-dropdown-menu>
        //         </el-dropdown>
        //         // <el-button onClick={() => this.setUpdate(row)} type="text">
        //         //   {BTN_TEXT.MODIFY}
        //         // </el-button>,
        //         // <el-button onClick={() => this.setDelete(row)} type="text">
        //         //   {BTN_TEXT.DELETE}
        //         // </el-button>
        //       ];
        //     }
        //   }
        // }
      ],
      modelData: {},
      modelOption: [],
      typeData: {},
      typeOption: [],
      charOption: [
        {
          value: 1,
          label: VARIABLE.YES
        },
        {
          value: 0,
          label: VARIABLE.NO
        }
      ],
      // 保存搜索内输入的搜索条件，防止翻页时变回无条件查询
      savedSearchCondition: []
    };
  },
  computed: {},
  created() {
    this.getAllModel();
    this.getAllType();
  },
  mounted() {
    // 调用设备列表查询
    this.findList();
  },
  watch: {
    // 切换模态框显示和隐藏
    dialogVisibleUpdate(val) {
      if (val === false) {
        this.$refs.updateForm.resetFields();
      }
    }
  },
  methods: {
    // 按钮触发事件
    buttonClickEvent(type) {
      // if (type === 'exit') {
      //   this.$emit('closePage');
      //   return;
      // }
      this[`${type}`]();
    },
    /**
     * @description: 操作下拉事件
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    handleCommand(command, row) {
      switch (command) {
        case 'info':
          this.setUpdate(row);
          break;
        case 'delete':
          this.setDelete(row);
          break;
        default:
          break;
      }
    },
    async getAllModel() {
      try {
        const res = await extendPropertyGetListExtendPropertyTypeAction();
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.modelData = res.rows;
          var arr = [];
          for (var key in res.rows) {
            var json = {};
            json.value = Number(key);
            json.label = res.rows[key];
            arr.push(json);
          }
          this.modelOption = arr;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    async getAllType() {
      try {
        const res = await extendPropertyGetListExtendPropertyDataTypeAction();
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.typeData = res.rows;
          var arr = [];
          for (var key in res.rows) {
            var json = {};
            json.value = Number(key);
            json.label = res.rows[key];
            arr.push(json);
          }
          this.typeOption = arr;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 点击批量删除
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    deteleall() {
      if (this.dataChecks.length === 0) {
        this.$message.error(MSG.PLEASE_CHOOSE_DELETE);
      } else {
        var arrdelete = [];
        this.dataChecks.map(item => arrdelete.push(item.extendId));
        this.$confirm(MSG.CONFIRM_CHOOSE_DELETE, TITLE.PROMPT, {
          confirmButtonText: BTN_TEXT.CONFIRM,
          cancelButtonText: BTN_TEXT.CANCEL,
          type: 'warning'
        })
          .then(() => {
            this.setDeleteChecks(arrdelete);
          })
          .catch(() => { });
      }
    },
    /**
     * @description: 执行批量删除的方法
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    async setDeleteChecks(param) {
      try {
        const res = await extendPropertyDeleteByIdsAction({
          ids: param
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          showSuccessMessage(res.message);
          this.findList();
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 获取批量勾选的数据
     * @param data // 勾选数据
     * @return: 无
     * @author: ZJM
     */
    tableChooseCheck(data) {
      this.dataChecks = data;
    },
    /**
     * @description: table分页
     * @param currentPage 第几页
     * @param pageSize 一页几条
     * @return: 无
     * @author: ZJM
     */
    onTablePageChange(currentPage, pageSize) {
      // 点击页数写入table分页配置
      this.tablePage.currentPage = currentPage;
      // 一页条数夜路table分页配置
      this.tablePage.pageSize = pageSize;
      // 调用查询接口
      this.findList();
    },
    /**
     * @description: 查询所属参数
     * @param val
     * @return: 无
     * @author: ZJM
     */
    async findList() {
      try {
        // 显示table加载动画
        this.tableLoading = true;

        const params = {
          current: this.tablePage.currentPage,
          size: this.tablePage.pageSize
        };

        var conditionJson = {
          conditionJson1: [],
          operatorGroup: 1,
          conditionJson2: []
        };
        if (this.savedSearchCondition.length > 0) {
          conditionJson = searchCondetionFunc(...this.savedSearchCondition);
        }

        const res = await extendPropertyGetAllAction(params, conditionJson);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将数据写入表单
          this.tableDatas = res.rows;
          this.tablePage.total = res.total;
          this.tableLoading = false;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 显示修改模态框
     * @param row 行数据
     * @return: 无
     * @author: ZJM
     */
    setUpdate() {
      var row = {};

      if (!this.$refs.xGrid.getRadioRecord()) {
        showWarningMessage('请选择要操作的数据！');
        return false;
      } else {
        row = this.$refs.xGrid.getRadioRecord();
      }
      this.dialogVisibleUpdate = true;
      this.formUpdate = JSON.parse(JSON.stringify(row));
    },
    /**
     * @description: 删除参数
     * @param row 行数据
     * @return: 无
     * @author: ZJM
     */
    setDelete() {
      var row = {};

      if (!this.$refs.xGrid.getRadioRecord()) {
        showWarningMessage('请选择要操作的数据！');
        return false;
      } else {
        row = this.$refs.xGrid.getRadioRecord();
      }
      // 确认是否删除
      this.$confirm(MSG.CONFIRM_DELETE, TITLE.PROMPT, {
        confirmButtonText: BTN_TEXT.CONFIRM,
        cancelButtonText: BTN_TEXT.CANCEL,
        type: 'warning'
      })
        .then(() => {
          this.setDeleteChecks([row.extendId]);
        })
        .catch(() => { });
    },
    addForm() {
      this.showPage = 1;
    },
    /**
     * @description: 提交修改
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    submitUpdate() {
      this.$refs.updateForm.validate(valid => {
        // 若验证通过
        if (valid) {
          this.submitUpdateFunc();
        }
      });
    },
    /**
     * @description: 保存数据
     * @return:
     * @author: ZJM
     */
    async submitUpdateFunc() {
      try {
        const form = {};
        form.extendId = this.formUpdate.extendId;
        form.propertyCode = this.formUpdate.propertyCode;
        form.extendType = this.formUpdate.extendType;
        form.extendName = this.formUpdate.extendName;
        form.propertyType = this.formUpdate.propertyType;
        form.propertyLength = this.formUpdate.propertyLength - 0;
        form.propertyDecimalPoint = this.formUpdate.propertyDecimalPoint - 0;
        form.propertyIsNull = this.formUpdate.propertyIsNull;
        form.remark = this.formUpdate.remark;
        const res = await extendPropertyUpdateByIdAction(form);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 提示正常信息
          showSuccessMessage(res.message);
          this.dialogVisibleUpdate = false;
          this.findList();
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 返回
     * @param val 0为显示index组件
     * @return:
     * @author: ZJM
     */
    goBack(val) {
      // 显示组件
      this.showPage = val;
      // 调用查询接口
      this.findList();
    },
    formatterIsOrNo({ cellValue }) {
      if (cellValue === 0) {
        return VARIABLE.NO;
      } else if (cellValue === 1) {
        return VARIABLE.YES;
      }
    },
    /**
     * @description: 显示模块值
     * @return:
     * @author: ZJM
     */
    formatterModel({ cellValue }) {
      return this.modelData[cellValue];
    },
    /**
     * @description: 显示参数类型值
     * @return:
     * @author: ZJM
     */
    formatterType({ cellValue }) {
      return this.typeData[cellValue];
    },
    /**
     * @description: 根据参数类型显示日期和长度
     * @return:
     * @author: ZJM
     */
    formatterLength({ row, cellValue }) {
      if (row.propertyType === 3) {
        return FORM_CONFIG_DATE[cellValue];
      } else {
        return cellValue;
      }
    },
    refreshTable() {
      this.findList();
    },
    /**
     * @description: 刷新表格数据
     * @param 无
     * @return void
     * @author: cyb
     */
    refreshTableData(savedSearchCondition) {
      if (savedSearchCondition) {
        this.savedSearchCondition = [...savedSearchCondition];
        this.tablePage.currentPage = 1;
      }
      this.findList();
    }
  }
};
